<template>
  <div class="inquiries">
    <j-header title="平仓查询"></j-header>
    <main>
      <nav v-show="tableData.length > 0">
        <p>商品名称</p>
        <p>金额</p>
        <p>方向</p>
        <p>盈亏</p>
      </nav>
      <div class="container" ref="inquiries">
        <ul>
          <router-link v-for="(obj, index) in tableData" :key='index' :to="{name: 'queryDetails', params: obj}" class="wave" tag="li">
            <p><span>{{ obj.cname }}</span></p>
            <p><span>￥{{ obj.orderamount }}</span></p>
            <p><span :style="{ background: obj.state * 1 ? '#58bc58' : '#f45252' }">{{ obj.state*1 ? '买跌' : '买涨' }}</span></p>
            <p class="left-arrow"><span :style="{ color: obj.winmoney*1 < 0 ? '#58bc58' : '#f45252' }">{{ obj.winmoney }}</span></p>
          </router-link>
        </ul>
      </div>
      <j-nothing
        :show="tableData.length < 1"
        :url="require('@/assets/inquiries/nothing.png')"
        title="没有更多数据了~"
        router="homepage">
      </j-nothing>
    </main>
  </div>
</template>

<script>
// @ is an alias to /src
import JHeader from '@/components/JHeader'
import JNothing from '@/components/JNothing'
import BScroll from 'better-scroll'
// import { scroll } from '@/utils'

export default {
  name: 'inquiries',
  components: {
    JHeader,
    JNothing
  },
  data () {
    return {
      tableData: [],
      scrollTop: 0,
      scroll: ''
    }
  },
  created () {
    this.init({ 'X-Pagination-Per-Page': 20 })
  },
  methods: {
    init (obj) {
      this.request({
        url: '/orders',
        headers: obj || {},
        loading: true
      }).then(res => {
        if (res.code === 1) {
          this.tableData = res.data
          this.$loading.end()
        }
      })
    }
  },
  activated () {
    // this.$refs.inquiries.scrollTop = this.scrollTop
    // this.scroll.scrollTo(0, this.scrollTop)
  },
  deactivated () {
  },
  // beforeRouteEnter (to, from, next) {
  //   to.meta.keepAlive = true
  //   next()
  // },
  mounted () {
    // const container = document.querySelector('.inquiries .container')
    // scroll(container, page => {
    //   // this.init({ 'X-Pagination-Per-Page': page * 10 })
    // }, scrollTop => {
    //   this.scrollTop = scrollTop
    // })
    var count = 2
    this.$nextTick().then(res => {
      this.scroll = new BScroll(this.$refs.inquiries, {
        scrollY: true,
        scrollX: true,
        momentum: true,
        bounce: true,
        click: true,
        pullDownRefresh: {
          threshold: 50, // 当下拉到超过顶部 50px 时，触发 pullingDown 事件
          stop: 0 // 刷新数据的过程中，回弹停留在距离顶部还有 20px 的位置
        },
        pullUpLoad: {
          threshold: -20 // 在上拉到超过底部 20px 时，触发 pullingUp 事件
        }
      })
      this.scroll.on('pullingDown', async () => {
        count = 2
        await this.init({ 'X-Pagination-Per-Page': 20 })
        this.scroll.finishPullDown()
      })
      this.scroll.on('pullingUp', async () => {
        count++
        await this.init({ 'X-Pagination-Per-Page': count * 10 })
        this.scroll.finishPullUp()
      })
      this.scroll.on('scrollEnd', params => {
        this.scrollTop = params.y
      })
    })
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
